@font-face {
  font-family: "Open Sans";
  src: url("fonts/opensans.woff2") format("woff2"), url("fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("fonts/opensansbold.woff2") format("woff2"), url("fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  font-weight: 700;
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 100%;
}
.main-nav__button-wrapper {
  position: relative;
  width: 320px;
  box-sizing: border-box;
  padding: 0 30px;
  margin: 0 auto;
}
.main-nav__logo {
  display: block;
  width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid #ececec;
}
.main-nav__logo-image {
  cursor: pointer;
  display: block;
  width: 86px;
  height: auto;
  margin: 22px auto;
}
.main-nav__logo-image:hover {
  opacity: 0.6;
}
.main-nav__logo-image:focus {
  opacity: 0.6;
}
.main-nav__logo-image:active {
  opacity: 0.3;
}
.main-nav__note {
  display: none;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle {
    position: absolute;
    top: 10px;
    right: 30px;
    z-index: 2;
    display: block;
    width: 20px;
    height: 14px;
    border: none;
    cursor: pointer;
    background-color: #ffffff;
    background-image: url("../img/icon-menu-open.svg");
    background-repeat: no-repeat;
  }
  .main-nav--opened .main-nav__toggle {
    position: absolute;
    top: 7px;
    right: 30px;
    z-index: 2;
    display: block;
    width: 20px;
    height: 20px;
    border: none;
    cursor: pointer;
    background-color: #ffffff;
    background-image: url("../img/icon-menu-close.svg");
    background-repeat: no-repeat;
  }
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs .site-list__item--add {
    display: none;
  }
  .main-nav--nojs .user-list__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    max-height: 88px;
    border-bottom: 1px solid #ececec;
  }
  .main-nav__logo {
    width: 321px;
    margin: 0;
    margin-top: 22px;
    border-bottom: none;
  }
  .main-nav__logo-image {
    width: 113px;
    margin: 0;
  }
  .main-nav__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 768px;
    padding: 0 50px;
    box-sizing: border-box;
    margin: 0 auto;
  }
  .main-nav__note {
    font-weight: 400;
    display: block;
    margin: 0;
    padding-top: 16px;
    order: 3;
    font-size: 15px;
    line-height: 24px;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__wrapper {
    width: 1150px;
    padding-left: 46px;
  }
  .main-nav__logo {
    width: 150px;
    order: 1;
    margin: 0 auto;
    margin-top: 8px;
    padding-left: 5px;
  }
  .main-nav__logo-image {
    width: 149px;
  }
  .main-nav__note {
    order: 5;
    margin-left: auto;
  }
}
.site-list {
