}
.user-list__icon-search {
  position: absolute;
  top: 30px;
  left: 34px;
  width: 17.6px;
  height: 17.6px;
  fill: rgba(34, 31, 31, 0.2);
  cursor: pointer;
}
.user-list__icon-search:hover {
  fill: #63d1bb;
}
.user-list__icon-search:focus {
  fill: #63d1bb;
}
.user-list__icon-search:active {
  fill: #63d1bb;
  opacity: 0.3;
}
.user-list__link--cart {
  position: relative;
}
.user-list__link--cart:hover .user-list__icon-cart {
  fill: #63d1bb;
}
.user-list__link--cart:focus .user-list__icon-cart {
  fill: #63d1bb;
}
.user-list__link--cart:active .user-list__icon-cart {
  fill: #63d1bb;
  opacity: 0.3;
}
.user-list__icon-cart {
  position: absolute;
  top: 30px;
  left: 31px;
  width: 20.294px;
  height: 18.849px;
  fill: rgba(34, 31, 31, 0.2);
  cursor: pointer;
}
.user-list__icon-cart:hover {
  fill: #63d1bb;
}
.user-list__icon-cart:focus {
  fill: #63d1bb;
}
.user-list__icon-cart:active {
  fill: #63d1bb;
  opacity: 0.3;
}
.user-list a[href]:hover {
  color: rgba(0, 0, 0, 0.6);
}
.user-list a[href]:focus {
  color: rgba(0, 0, 0, 0.6);
}
.user-list a[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .user-list__icon-search {
    top: 37px;
    left: 34px;
  }
  .user-list__icon-cart {
