.substrate {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 30;
}
.substrate--show {
  display: block;
}
.add-to-cart {
  position: relative;
  margin: auto;
  width: 100%;
  min-height: 337px;
  background-color: #ffffff;
  box-shadow: 0 5px 8px 0 rgba(0, 1, 1, 0.25);
  z-index: 500;
  border-top: 1px solid #c6c6c6;
  border-bottom: 1px solid #c6c6c6;
}
.add-to-cart__wrapper {
  width: 320px;
  box-sizing: border-box;
  padding: 30px 30px 0 30px;
  margin: 0 auto;
}
.add-to-cart__title {
  font-weight: 700;
  width: 155px;
  margin: 0 auto;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}
.add-to-cart__description {
  font-weight: 400;
  margin: 0;
  padding: 12px 3px 23px 0;
  font-size: 15px;
  line-height: 30px;
  color: #000000;
  text-align: center;
}
.add-to-cart__size-buttons {
  display: flex;
  padding-bottom: 32px;
}
.add-to-cart__size {
  font-weight: 700;
  display: block;
  width: 82px;
  height: 52px;
  box-sizing: border-box;
  margin-right: 7px;
  color: #444444;
  font-size: 17px;
  line-height: 16px;
  border: 2px solid #ececec;
  background-color: #ffffff;
}
.add-to-cart__size:hover {
  border: 2px solid #c7c7c7;
}
.add-to-cart__size:focus {
  border: 2px solid #c7c7c7;
  cursor: pointer;
}
.add-to-cart__size:active {
  border: 2px solid #63d1bb;
}
.add-to-cart__size:nth-child(3n) {
  margin-right: 0;
}
.add-to-cart__size--active {
  border: 2px solid #63d1bb;
  cursor: pointer;
}
.add-to-cart__size--active:hover {
  border: 2px solid #63d1bb;
}
.add-to-cart__size--active:focus {
  border: 2px solid #63d1bb;
}
.add-to-cart__size--active:active {
  border: 2px solid #63d1bb;
}
.add-to-cart__button {
  font-size: 17px;
  line-height: 16px;
  padding-top: 17px;
  padding-bottom: 17px;
  color: #ffffff;
  border: none;
  background-color: #63d1bb;
}
@media (min-width: 768px) {
  .add-to-cart {
    width: 566px;
    min-height: 336px;
    border: 1px solid #c6c6c6;
  }
  .add-to-cart__wrapper {
    width: 354px;
    box-sizing: border-box;
    padding: 0;
    margin: 0 auto;
  }
  .add-to-cart__title {
    padding-top: 33px;
    width: 100%;
    font-size: 30px;
    line-height: 57px;
  }
  .add-to-cart__description {
    font-size: 17px;
  }
  .add-to-cart__size {
    width: 114px;
  }
}
.modal-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.9);
  overflow-y: auto;
}
.modal-overlay--show {
  display: flex;
}
